<template>
  <div class="search-results" v-if="results.length">
    <h2>查询结果——共查询到 {{ results.length }} 条结果</h2>
    <div class="table-responsive">
      <table class="table">
        <thead>
          <tr>
            <th>课程名称</th>
            <th>课程属性</th>
            <th>公选课领域</th>
            <th>授课老师</th>
            <th>课程内容与评价</th>
            <th>期末考核方式</th>
            <th>课程成绩</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(row, index) in paginatedResults" :key="index"
              :class="{'highlight-row': row['课程属性'] === '通识选修课（公选课）'}">
            <td class="heiti-font">{{ row['课程名称'] }}</td>
            <td class="heiti-font">{{ row['课程属性'] }}</td>
            <td class="heiti-font">{{ row['公选课领域'] }}</td>
            <td class="heiti-font">{{ row['授课老师'] }}</td>
            <td class="heiti-font">{{ row['课程内容与评价'] }}</td>
            <td class="heiti-font">{{ row['期末考核方式'] }}</td>
            <td class="times-bold-font">{{ row['课程成绩'] }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="pagination">
      <button @click="prevPage" :disabled="currentPage === 1">上一页</button>
      <span>第 {{ currentPage }} 页，共 {{ totalPages }} 页</span>
      <button @click="nextPage" :disabled="currentPage === totalPages">下一页</button>
      <input type="number" v-model.number="inputPage" @keyup.enter="jumpToPage" placeholder="页码" min="1" :max="totalPages" />
      <button @click="jumpToPage">跳转</button>
    </div>
  </div>
  <div v-else>
    <h2>未找到查询结果</h2>
  </div>
</template>

<script>
export default {
  props: ['results'],
  data() {
    return {
      currentPage: 1,
      itemsPerPage: 15,
      inputPage: ''
    };
  },
  computed: {
    totalPages() {
      return Math.ceil(this.results.length / this.itemsPerPage);
    },
    paginatedResults() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.results.slice(start, end);
    }
  },
  methods: {
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    },
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    jumpToPage() {
      if (this.inputPage >= 1 && this.inputPage <= this.totalPages) {
        this.currentPage = this.inputPage;
      }
    }
  }
};
</script>

<style>
.search-results {
  margin-top: 20px;
}
.table-responsive {
  overflow-x: auto;
}
.table {
  width: 100%;
  border-collapse: collapse;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  overflow: hidden;
}
.table th, .table td {
  padding: 10px;
  text-align: left;
  border: 1px solid #ddd;
}
.table th {
  background-color: #007bff;
  color: white;
}
.table th:first-child {
  border-top-left-radius: 10px;
}
.table th:last-child {
  border-top-right-radius: 10px;
}
.table tr:nth-child(even) {
  background-color: #f9f9f9;
}
.table tr:hover {
  background-color: #f1f1f1;
}
.table tr:last-child td:first-child {
  border-bottom-left-radius: 10px;
}
.table tr:last-child td:last-child {
  border-bottom-right-radius: 10px;
}
.highlight-row {
  color: rgb(135, 135, 255);
  background-color: #e0f7fa; /* 适宜与蓝色字体的背景色 */
}
.heiti-font {
  font-family: "Heiti SC", "黑体", sans-serif;
}
.times-bold-font {
  font-family: "Times New Roman", Times, serif;
  font-weight: bold;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .table th, .table td {
    padding: 8px;
    font-size: 14px;
  }
  .table th, .table td {
    white-space: nowrap;
  }
  .table tr {
    display: block;
    margin-bottom: 10px;
  }
  .table tr td {
    display: flex;
    justify-content: space-between;
    padding: 8px;
    border: 1px solid #ddd;
  }
  .table thead {
    display: none;
  }
  .table td::before {
    content: attr(data-label);
    font-weight: bold;
    width: 50%;
    display: inline-block;
  }
}

.pagination {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.pagination button {
  padding: 10px 20px;
  margin: 0 5px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  border-radius: 5px;
}
.pagination button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}
.pagination span {
  margin: 0 10px;
}
.pagination input {
  width: 50px;
  padding: 5px;
  text-align: center;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin: 0 5px;
}
</style>
